button {
  padding: 20px;
  margin: 20px;
  border: 1px solid rgb(102, 15, 20);
  border-radius: 15px;
}

button:hover {
  background-color: rgb(57, 16, 18);
  color: white;
}

button:focus {
  outline: none;
  background-color: rgb(57, 16, 18);
  color: white;

}



